import React from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap';
import { Link } from 'react-router-dom';
import './navbarindex.css';

export default class NavBarComp extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  render() {
    return (
      <div>
        <Navbar expand="md">
          <NavbarBrand href="/">LC</NavbarBrand>
          <NavbarToggler onClick={this.toggle}><ion-icon name="menu"></ion-icon></NavbarToggler>
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav navbar>
            <NavItem>
                <NavLink><a data-scroll href="#AboutMe">About Me</a></NavLink>
              </NavItem>
              <NavItem>
                <NavLink><a data-scroll href="#Projects">Projects</a></NavLink>
              </NavItem>
              <NavItem>
                <NavLink><a data-scroll href="#Resume">Resume</a></NavLink>
              </NavItem>
              <NavItem>
                <NavLink><a data-scroll href="#ContactMe">Contact Me</a></NavLink>
              </NavItem>
            </Nav>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink><a target="_blank" to="https://github.com/Lindca"><ion-icon name="logo-github"></ion-icon></a></NavLink>
              </NavItem>
              <NavItem>
                <NavLink><a target="_blank" href="https://www.linkedin.com/in/lindsay-campbell-06517417b/"><ion-icon name="logo-linkedin"></ion-icon></a></NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}